<template>
  <div class="menu">
    <div class="menu-swip">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item,index) in swipData" :key="index">
          <img :src="item.bannerImg" alt class="auto-img" @click="goDetail(item.pid)" />
          <div class="img-name">{{item.name}}</div>
        </van-swipe-item>
      </van-swipe>
    </div>
    <van-sticky>
      <div class="menu-nav">
        <van-grid :border="false" :style="{width:maxwidth+'rem'}">
          <van-grid-item
            v-for="(item,index) in typeData"
            :key="index"
            @click="switchNav(item)"
            :ref="'menuNav'+index"
            :style="{width:width+'rem'}"
          >
            <div class="nan-img">
              <img :src="item.isActive ? item.activeIcon : item.inactiveIcon" class="auto-img" />
            </div>
            <div class="title">{{item.typeDesc}}</div>
          </van-grid-item>
        </van-grid>
      </div>
    </van-sticky>
    <div class="menu-box">
      <div
        class="menu-list clearfix"
        v-for="(item,index) in productsData"
        :key="index"
        @click="goDetail(item.pid)"
      >
        <div class="menu-img fl">
          <img class="auto-img" :src="item.smallImg" alt />
        </div>
        <div class="menu-content fl clearfix">
          <div class="menu-name">{{item.name}}</div>
          <div class="menu-enname">{{item.enname}}</div>
          <div class="menu-desc one-text">简介:{{item.desc}}</div>
          <span class="menu-pri">￥{{item.price}}</span>
        </div>
      </div>
      <van-empty description="暂无商品" v-show="isEmpty" />
    </div>
  </div>
</template>

<script>
import "../mainViews/less/menu.less";
export default {
  name: "Menu",
  data() {
    return {
      active: 0,
      width: 80 / 37.5,
      maxwidth: 0,
      swipData: [],
      typeData: [],
      productsData: [],
      isEmpty: false,
    };
  },
  created() {
    this.getSwip();
    this.getType();
  },
  methods: {
    //获取轮播图
    getSwip() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();
          //
          if (result.data.code == 300) {
            this.swipData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //获取到切换商品类型
    getType() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/type",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 400) {
            let menuNav = [
              {
                type: "coffee",
                icon: {
                  active: require("../mainViews/images/coffeeactive.png"),
                  inactive: require("../mainViews/images/coffeeinactive.png"),
                },
              },
              {
                type: "latte",
                icon: {
                  active: require("../mainViews/images/latteractive.png"),
                  inactive: require("../mainViews/images/latterinactive.png"),
                },
              },
              {
                type: "rena_ice",
                icon: {
                  active: require("../mainViews/images/nabinactive.png"),
                  inactive: require("../mainViews/images/nabininactive.png"),
                },
              },
            ];

            //按照类型把menuNav数据添加进result数据里
            result.data.result.map((v) => {
              for (let i = 0; i < menuNav.length; i++) {
                if (v.type == menuNav[i].type) {
                  v.inactiveIcon = menuNav[i].icon.inactive;
                  v.activeIcon = menuNav[i].icon.active;
                  v.isActive = false;
                  break;
                }
              }
            });

            result.data.result.unshift({
              type: "isHot",
              typeDesc: "推荐",
              activeIcon: require("../mainViews/images/recommendactive.png"),
              inactiveIcon: require("../mainViews/images/recommendinactive.png"),
              isActive: true,
            });
            result.data.result.push({
              type: "juice",
              typeDesc: "果汁",
              activeIcon: require("../mainViews/images/juiceactive.png"),
              inactiveIcon: require("../mainViews/images/juiceinactive.png"),
              isActive: false,
            });
            result.data.result.push({
              type: "hearth",
              typeDesc: "健康轻食",
              activeIcon: require("../mainViews/images/qinshiactive.png"),
              inactiveIcon: require("../mainViews/images/qinshiinactive.png"),
              isActive: false,
            });

            this.typeData = result.data.result;
            this.getProductsData(this.typeData[0]);//根据相应的商品类型获取到相应的商品数据

            this.$nextTick(() => {
              this.maxwidth = this.width * this.typeData.length;
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //切换商品类型得到相应数据
    switchNav(item) {
      if (item.isActive) {
        return;
      }
      for (let i = 0; i < this.typeData.length; i++) {
        if (this.typeData[i].isActive) {
          this.typeData[i].isActive = false;
          break;
        }
      }
      item.isActive = true;

      this.getProductsData(item); //根据商品类型获取数据
    },
    //根据点击item类型获取商品信息
    getProductsData(item) {
      this.productsData = [];

      //判断缓存到本地sessionStorage，如果有缓存则不需要再次加载，在缓存中获取
      let data = sessionStorage.getItem(item.type);
      if (data) {
        this.isEmpty = false;
        this.productsData = JSON.parse(data);
        if (this.productsData == false) {
          this.isEmpty = true;
        }
        return;
      }

      let params = {
        appkey: this.appkey,
        key: "type",
        value: item.type,
      };
      if (item.type == "isHot") {
        params.key = "isHot";
        params.value = 1;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        params,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 500) {
            this.isEmpty = false;
            this.productsData = result.data.result;
            if (this.productsData == false) {
              this.isEmpty = true;
            }
            //缓存商品数据
            sessionStorage.setItem(
              item.type,
              JSON.stringify(this.productsData)
            );
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //点击携带商品id获取详情
    goDetail(pid) {
      this.$router.push({ name: "Detail", query: { pid: pid } });
    },
  },
};
</script>

<style lang="less" scoped>
</style>